<template>
    <div class="container">
        <div class="row">
            <div class="col-xs-12">
                <v-breadnav>惠鑫贷详情</v-breadnav>
            </div>
        </div>
        <div class="row">
            <div class="col-xs-12">
                <section class="loan-form">
                    <form action="" id="loan">
                       <div class="form-group clearfix">
                            <label for="inputEmail3" class="col-xs-2 control-label"><span class="require">*</span>车辆品牌</label>
                            <div class="col-xs-10">
                                <select name="" id="brand" class="form-control" @change="getCarSeries">
                                    <option value="0" selected>请选择车辆品牌</option>
                                    <option v-for="(item,index) in brand" :key="index" :value="item.id">{{item.name}}</option>
                                </select>
                            </div>
                        </div>
                        <div class="form-group clearfix">
                            <label for="inputtext3" class="col-xs-2 control-label"><span class="require">*</span>购买车系</label>
                            <div class="col-xs-10">
                                <select name="" id="carSeries" class="form-control">
                                    <option value="0" selected>请选择车系（请先选择车辆品牌）</option>
                                    <option v-for="(item,index) in brandSeries" :key="index" :value="item.id">{{item.name}}</option>
                                </select>
                            </div>
                        </div>
                        <div class="form-group clearfix">
                            <label for="inputtext3" class="col-xs-2 control-label"><span class="require">*</span>省&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;份</label>
                            <div class="col-xs-10">
                            <select name="" id="province" class="form-control" @change="getCity">
                                <option value="0" selected>请选择省份</option>
                                <option v-for="(item,index) in province" :key="index" :value="item.id">{{item.name}}</option>
                            </select>
                            </div>
                        </div>
                        <div class="form-group clearfix">
                            <label for="inputtext3" class="col-xs-2 control-label"><span class="require">*</span>城&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;市</label>
                            <div class="col-xs-10">
                                <select name="" id="city" class="form-control" @change="getProvince">
                                    <option value="0" selected>请选择城市</option>
                                    <option v-for="(item,index) in city" :key="index" :value="item.id" :data-pid="item.pid">{{item.name}}</option>
                                </select>
                            </div>
                        </div>
                         <div class="form-group clearfix">
                            <label for="inputtext3" class="col-xs-2 control-label"><span class="require">*</span>真实姓名</label>
                            <div class="col-xs-10">
                            <input type="text" class="form-control" id="name" placeholder="请输入您的真实姓名">
                            </div>
                        </div>
                         <div class="form-group clearfix">
                            <label for="inputtext3" class="col-xs-2 control-label"><span class="require">*</span>身份证号</label>
                            <div class="col-xs-10">
                            <input type="text" class="form-control" id="idCard" placeholder="请输入您的身份证号">
                            </div>
                        </div>
                         <div class="form-group clearfix">
                            <label for="inputtext3" class="col-xs-2 control-label"><span class="require">*</span>年&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;龄</label>
                            <div class="col-sm-10">
                            <input type="number" class="form-control" id="age" placeholder="请输入您的年龄">
                            </div>
                        </div>
                         <div class="form-group clearfix ">
                            <label for="inputtext3" class="col-xs-2 control-label"><span class="require">*</span>性&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;别</label>
                            <div class="col-xs-10 sex">
                            <label class="radio-inline">
                                <input type="radio" name="sex" value="1" checked> 男
                            </label>
                            <label class="radio-inline">
                                <input type="radio" name="sex"  value="0"> 女
                            </label>
                            </div>
                        </div>
                        <div class="form-group clearfix">
                            <label for="inputtext3" class="col-xs-2 control-label"><span class="require">*</span>联系方式</label>
                            <div class="col-xs-10">
                                 <input type="number" class="form-control" id="phone"  placeholder="请输入您的手机号码">
                            </div>
                        </div>
                        <div class="form-group clearfix">
                            <label for="inputtext3" class="col-xs-2 control-label"><span class="require">*</span>补充说明</label>
                            <div class="col-xs-10">
                                 <textarea name="" id="mark" cols="30" rows="5" class="form-control" placeholder="请输入补充说明"></textarea>
                                 <p>为了不影响您的申请回馈，请您务必填写真实信息，我们会尽快向您提供协助（带*为必填真实信息选项）</p>
                            </div>
                        </div>
                        <div class="form-group clearfix">
                            <div class="col-xs-12">
                                <button type="button" class="btn" @click="resetForm">清空</button>
                                <button type="button" class="btn" @click="submit">完成并提交</button>
                            </div>                           
                        </div>
                    </form>
                </section>
            </div>
        </div>
    </div>
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
import {PROVINCE} from '@/assets/js/province.json'
import {CITIES} from '@/assets/js/city.json'
export default {
    data:function(){
        return {
            province:PROVINCE,
            city:CITIES,
            brand:[],
            brandSeries:[],
            financeId:this.$route.params.id,
        }
    },
    components:{    
        vBreadnav
    },
    methods:{
         getCity:function(){
            let provinceId = $("#province").find("option:selected").val();
            let cities = this.city.filter(function(n){
                return n.pid == provinceId;
            });
            this.city = cities;
        },
        getProvince:function(){
             let provinceId = $("#city").find("option:selected").attr('data-pid');
             let provinces = $("#province").find("option");
             $.each(provinces,function(i,n){
                 let id = $(n).val();
                 if(id == provinceId){
                     $(n).attr("selected",true);
                 }
             })
        },
        getBrand:function(){
            let _this = this;
            $.ajax({
                url:PC_COMMONURL+'/queryAllBrandData',
                type:"get",
                data:{platForm:"020003"},
                dataType:'json',
                asnyc:true,
                success:function(data){
                    if(data.code == SUCCESS_CODE){
                        console.log(data.data);
                        _this.brand = data.data.brandList;
                    }
                }
            })
        },
        getCarSeries:function(){
            let _this = this;
            let brandId = $("#brand").find("option:selected").val();
            $.each(this.brand,function(i,n){
                if(n.id == brandId){
                    _this.brandSeries = n.models;
                }
            })
        },
        resetForm:function(){
            $('select','#loan').each(function(i,n){
                debugger;
                $(n).find("option:first-child").prop('selected',true);
            })
            $("input",'#loan').each(function(i,n){
                $(n).val("");
            })
            $('textarea').val("");
        },
        submit:function(){
            let data = new Object;
            data.financeId = this.financeId;
            data.brandId = $("#brand").find("option:selected").val();
            if(data.brandId == 0){
                $('.form-group').eq(0).addClass("has-error");
                return;
            }else{
                $('.form-group').eq(0).removeClass("has-error");
            }
            data.brandSeriesId = $('#carSeries').find("option:selected").val();
            if(data.brandSeriesId == 0){
                $('.form-group').eq(1).addClass("has-error");
                return;
            }else{
                $('.form-group').eq(1).removeClass("has-error");
            }
            data.provinceId = $("#province").find("option:selected").val();
            if(data.provinceId == 0){
                $('.form-group').eq(2).addClass("has-error");
                return;
            }else{
                $('.form-group').eq(2).removeClass("has-error");
            }
            data.cityId = $("#city").find("option:selected").val();
            if(data.cityId == 0){
                $('.form-group').eq(3).addClass("has-error");
                return;
            }else{
                $('.form-group').eq(3).removeClass("has-error");
            }
            data.name = $("#name").val();
            if(data.name == ''){
                $('.form-group').eq(4).addClass("has-error");
                return;
            }else{
                $('.form-group').eq(4).removeClass("has-error");
            }
            data.cardNo = $("#idCard").val();
            if(data.cardNo == ''){
                $('.form-group').eq(5).addClass("has-error");
                return;
            }else{
                $('.form-group').eq(5).removeClass("has-error");
            }
            data.age = $("#age").val();
            if(data.age == ''){
                $('.form-group').eq(6).addClass("has-error");
                return;
            }else{
                $('.form-group').eq(6).removeClass("has-error");
            }
            data.sex = $(':radio[name="sex"]:checked').val();            
            data.mobile = $("#phone").val();
            if(data.mobile == ''){
                $('.form-group').eq(8).addClass("has-error");
                return;
            }else{
                $('.form-group').eq(8).removeClass("has-error");
            }
            data.mark = $("#mark").val();
             if(data.mark == ''){
                $('.form-group').eq(9).addClass("has-error");
                return;
            }else{
                $('.form-group').eq(9).removeClass("has-error");
            }
            for(let key in data){
                if(data[key] == ""){
                    alert("请填写必填项")
                }
            }
            $.ajax({
                url:PC_COMMONURL+'/submitFinanceApply',
                type:"post",
                data:data,
                success:function(data){
                    let json = JSON.parse(data);
                    if(json.code == SUCCESS_CODE){
                        alert(json.message);
                    }
                }
            })
        }
        
    },
    mounted:function(){
        this.getBrand();
    }
    
}
</script>



